}

.nav-main {
  width: 100%;
  height: min-content;
  background-color: #81B3D2;
  position: relative;
}

.menu-wrapper {
  order: 1;
  background-color: #ffffff;
}

.menu-container {
  position: relative;
  height: 56px;
  display: flex;
  flex-direction: column;
}

.site-list--toggle {
  position: absolute;
  right: 20px;
  top: 17px;
  width: 23px;
  height: 22px;
}

.site-list--toggle__button {
  width: 23px;
  height: 22px;
  fill: #81B3D2;
}

.site-list--toggle:hover .site-list--toggle__button,
.site-list--close:hover {
  opacity: 0.6;
}

.site-list--toggle:active .site-list--toggle__button,
.site-list--close:active {
  opacity: 0.3;
}

.site-list--close {
  position: absolute;
  top: 17px;
  right: 20px;
  width: 22px;
  height: 22px;
  cursor: pointer;
}

.site-list {
  list-style: none;
  margin: 0;
  padding: 0;
  height: 100%;
}

.site-list__item {
  width: 100%;
  height: 55px;
  box-shadow: 0px 1px 0px rgba(1, 1, 1, 0.2), inset 0px 1px 0px rgba(255, 255, 255, 0.2);
}

.site-list__item--link:hover {
  background-color: #669ec0;
}

.site-list__item--link:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}

.shadow {
  position: absolute;
  bottom: 0;
  height: 7px;
  width: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  mix-blend-mode: normal;
}

.site-list__item--link {
  text-decoration: none;
  background-color: none;
  color: #000000;
  margin: 0;
  padding: 0;
  display: block;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  line-height: 40px;
  color: #ffffff;
  font-weight: 700;
}

.nav__big-screen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  justify-content: space-between;
  z-index: 5;
}

.nav__big-screen__list--left,
.nav__big-screen__list--right {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  height: 100%;
  width: 50%;
  box-sizing: border-box;
}

.nav__big-screen__list--left {
  justify-content: flex-start;
}

.nav__big-screen__list--right {
  justify-content: flex-end;
}

.nav__big-screen__item {
  width: 40%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.nav__big-screen__item a {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #000000;
  font-weight: 700;
  line-height: 26px;
  font-size: 14px;
}

@media screen and (min-width: 768px) {
  .site-list--toggle {
    display: none;
  }
  .menu-container {
    margin-top: -55px;
  }
  .nav-wrapper,
  .nav-main {
    background-color: white;
    display: block;
  }
  .site-list {
    display: flex;
    flex-wrap: wrap;
    color: #000000;
    width: 100%;
  }
  .site-list__item {
    width: 20%;
    box-shadow: none;
  }
  .site-list__item:nth-of-type(2) {
    margin-right: auto;
  }
  .site-list__item--link {
    color: #000000;
    font-size: 14px;
    line-height: 26px;
    background-color: transparent;
  }
  .site-list__item--link:hover {
    color: #81B3D2;
    background-color: transparent;
  }
  .site-list__item--link:active {
    color: #766357;
  }
  .shadow {
    display: none;
  }
  .navigation--close {
    display: none;
  }
}
